<template>
    <div>
        <label class="b-24-black" v-html="t('account.title')"></label>
    </div>
    <form class="py-5" @submit.prevent="handleLogin">
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.name')"></label>
            <input minlength="3" type="text" class="form-control py-3" v-model="username" :placeholder="t('account.name')">
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.email')"></label>
            <input type="email" class="form-control py-3" v-model="email" :placeholder="t('account.email')">
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.date')"></label>
             <VueDatePicker input-class="form-control" :auto-apply="true" uid="birthday" :enable-time-picker="false" :format="formatDate" placeholder="MM/DD/YYYY" v-model="selectedDate"/>
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.phone')"></label>
            <input minlength="10" type="text" class="form-control py-3" v-model="phone" :placeholder="t('account.phone')">
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.genre')"></label>
            <select class="form-select form-select-md" v-model="genre" style="padding-top:.75rem; padding-bottom:.75rem;">
                <option value="0">Seleccione...</option>
                <option value="1">Masculino</option>
                <option value="2">Femenino</option>
                <option value="3">Prefiero no decirlo</option>
            </select>
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.password')"></label>
            <input minlength="8" @input="validatePassword" type="password" v-model="password" class="form-control py-3" id="pass">
            <div v-if="password && !isValid" class="text-danger mt-2 small">
             La contrasea debe tener al menos 8 caracteres, una mayscula, una minscula, un nmero y un smbolo.
            </div>

            <div v-if="isValid" class="text-success mt-2 small">
             Contrasea segura
            </div>
        </div>
        <div class="mb-3 px-3 text-start">
            <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('account.confirmPassword')"></label>
            <input minlength="8" @input="validateConfirm" v-model="confirmPassword" type="password" class="form-control py-3" id="confirmPassword">
            <div v-if="!passwordsMatch && isValid" class="text-danger small mt-1">
                 Las contraseas no coinciden
            </div>
            <div v-if="passwordsMatch && isValid" class="text-success small mt-1">
                 Las contraseas coinciden
            </div>
            <input class="mt-3" type="checkbox" @click="showPassword()">{{ t('account.showhide') }}
        </div>
        <div>
            <button type="submit" class="btn-black px-3" v-html="t('account.login')"></button>
        </div>
    </form>
    <!-- Modal de error -->
    <div v-if="showModal" class="modal">
        <div class="modal-content">
            <h3>{{ errorMessage }}</h3><br>
            <button class="btn-black px-3" @click="showModal = false">{{ t('account.btnCerrar') }}</button>
        </div>
    </div>
    <p class="mt-2">Fecha seleccionada: {{ formattedDate }}</p>
</template>

<script setup>
    import { useI18n } from 'vue-i18n' //multilenguaje
    import { useRouter } from 'vue-router' //redirect
    import { ref, computed } from 'vue' //general
    import VueDatePicker from '@vuepic/vue-datepicker'; //datepicker
    import '@vuepic/vue-datepicker/dist/main.css';
    import { sendVerificationEmail } from "@/services/emailService"; //correo
    import { API_BASE_URL } from '@/config/api'

    //multilenguaje
    const { t } = useI18n() 
    //datos
    const username = ref('')
    const email = ref('')
    const password = ref('')
    const phone = ref('')
    const genre = ref('')
    const confirmPassword = ref('')
    //validacin de contrasea
    // const isPasswordValid = ref(false)
    const passwordsMatch = ref(false)
    const isValid = ref(false)
    //modal
    const errorMessage = ref('')
    const showModal = ref(false)
    //router
    const router = useRouter()
    //datepicker
    const selectedDate = ref(null)
    //correo
    const sent = ref(false);

    /***********************Preparacin de datos***********************/
    function showPassword() {
        var x = document.getElementById("pass");
        var xx = document.getElementById("confirmPassword");
        if (x.type === "password") {
            x.type = "text";
            xx.type = "text";
        } else {
            x.type = "password";
            xx.type = "password";
        }
    }
    // funcin para dar formato personalizado
    const formatDate = (date) => {
        if (!date) return ''
        const d = new Date(date)
        const month = String(d.getMonth() + 1).padStart(2, '0')
        const day = String(d.getDate()).padStart(2, '0')
        const year = d.getFullYear()
        return `${month}/${day}/${year}`
    }

    /***********************Validacin***********************/
    const validatePassword = () => {
        const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^A-Za-z0-9]).{8,}$/
        isValid.value = regex.test(password.value)
    }
    const validateConfirm = () => {
        passwordsMatch.value = password.value == confirmPassword.value 
        console.log(passwordsMatch.value)
    }
    // const canSubmit = computed(() => isPasswordValid.value && passwordsMatch.value)
    const formattedDate = computed(() => formatDate(selectedDate.value))

    const validateForm = () => {
        // Validar nombre
        if (!username.value.trim()) {
            errorMessage.value = "El nombre es obligatorio.";
            showModal.value = true;
            return false;
        }

        // Validar email con expresin regular
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email.value)) {
            errorMessage.value = "El correo electrnico no es vlido.";
            showModal.value = true;
            return false;
        }

        // Validar fecha de nacimiento
        if (!selectedDate.value) {
            errorMessage.value = "Debe seleccionar una fecha de nacimiento.";
            showModal.value = true;
            return false;
        }

        // Validar telfono (solo nmeros y mnimo 10 dgitos)
        const phoneRegex = /^\d{10,}$/;
        if (!phoneRegex.test(phone.value)) {
            errorMessage.value = "El telfono debe tener al menos 10 dgitos numricos.";
            showModal.value = true;
            return false;
        }

        // Validar gnero
        if (!genre.value || genre.value === "0") {
            errorMessage.value = "Debe seleccionar un gnero.";
            showModal.value = true;
            return false;
        }

        // Validar contrasea segura
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^A-Za-z0-9]).{8,}$/;
        if (!passwordRegex.test(password.value)) {
            errorMessage.value =
            "La contrasea debe tener al menos 8 caracteres, una mayscula, una minscula, un nmero y un smbolo.";
            showModal.value = true;
            return false;
        }

        // Validar confirmacin de contrasea
        if (password.value !== confirmPassword.value) {
            errorMessage.value = "Las contraseas no coinciden.";
            showModal.value = true;
            return false;
        }

        // Si todo est correcto
        return true;
        };
    /***********************Envo***********************/
     const handleLogin = async () => {
        if (!validateForm()) return;

        console.log(username.value);
        console.log(email.value);
        console.log(password.value);
        console.log(formattedDate.value.replaceAll("/","-"));
        console.log(phone.value);
        console.log( genre.value);

        try {
            const response = await fetch(`${API_BASE_URL}'/api/usuarios/Create`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    name: username.value,
                    email: email.value,
                    password: password.value,
                    birthday: formattedDate.value.replaceAll("/","-"),
                    phone: phone.value,
                    genre: genre.value
                })
            })

            const data = await response.json()
                if (data.status == 1) {
                    localStorage.setItem('token', data.data)
                    await sendCode()
                    router.push('/') // Redirige al home
                } else {
                    errorMessage.value = data.message
                    showModal.value = true
                }
            } catch (error) {
                errorMessage.value = 'Error de conexin con el servidor'
            }
    }
    //Envo de cdigo
    const sendCode = async () => {
        const code = Math.floor(100000 + Math.random() * 900000);
        const ok = await sendVerificationEmail(email.value, "Usuario", code);

        if (ok) {
            sent.value = true;
            console.log("Cdigo generado:", code);
            // Aqu puedes guardar el cdigo temporalmente (por ejemplo, en tu API o en localStorage)
        }
    };
</script>

<style scoped>
    .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    padding:20px
    }
    .modal-content {
    background: white;
    padding: 40px;
    border-radius: 8px;
    }

    
</style>
